import React, {useLayoutEffect} from "react";
import {Canvas} from "@react-three/fiber";
import {OrbitControls} from "@react-three/drei";
import anime from "animejs";
import Geo from "./Goe";
import Lights from "../../components/Lights";

const WeAreBlock = ({nextPage}) => {
    useLayoutEffect(() => {
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '#we-are-content',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1000,
        });
        timeline.add({
            targets: 'canvas',
            easing: 'linear',
            opacity: [0, 1],
            scale: [0.5, 1],
            duration: 400,
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, [])
    return (
        <div id="we-are">
            <div className="grid lg:grid-cols-12 min-h-screen">
                <div id="we-are-content" className="col-span-6 flex justify-center items-center lg:items-end flex-col py-12 lg:py-0">
                    <div className="flex flex-col justify-start relative text-center sm:text-left w-full sm:w-3/4 lg:w-auto">
                        <div className="css--we-are-decor" style={{zIndex: 1}}>
                            <div className="css--we-are-decor-inner">
                                <img src="/img/discover_expand/rectangle_inner.svg" alt=""/>
                            </div>
                        </div>
                        <div className="css--we-are-header-first" style={{zIndex: 2}}>we are</div>
                        <div className="css--we-are-header-second" style={{zIndex: 2}}>cyborgs</div>
                    </div>
                    <div className="w-3/4 mt-10">Our creative/physical abilities are extended beyond normal human limitations by mechanical elements built into the body/mind.</div>
                    <div className="w-3/4 mt-12">
                        <button className="btn" onClick={nextPage}>How?</button>
                    </div>
                </div>
                <div className="col-span-6 h-screen hidden lg:block css--we-are-hero">
                    <div style={{width: "100%", height: "100%", position: "relative"}}>
                        <Canvas
                            gl={{alpha: true}}
                            camera={{position: [0, 0, 6], fov: 40}}>
                            <Lights/>
                            <OrbitControls makeDefault autoRotate autoRotateSpeed={0.5} zoomSpeed={0.1}/>
                            <React.Suspense fallback={null}>
                                <Geo />
                            </React.Suspense>
                        </Canvas>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default WeAreBlock;
